* {
  box-sizing: border-box;
  font-weight: normal;
}
html,
body {
  height: 100%;
}
body {
  margin: 0;
  font-family: "Actor", Helvetica, Arial, sans-serif;
  background-color: transparent;
  color: #e4e4e4;
  line-height: 1.45;
  display: flex;
  flex-direction: column;
}
@media (min-width: 0) {
  body {
    font-size: 17px;
  }
}
@media (min-width: 640px) {
  body {
    font-size: 18px;
  }
}
@media (min-width: 768px) {
  body {
    font-size: 19px;
  }
}
@media (min-width: 1024px) {
  body {
    font-size: 17px;
  }
}
@media (min-width: 1280px) {
  body {
    font-size: 18px;
  }
}
@media (min-width: 1600px) {
  body {
    font-size: 19px;
  }
}
a,
a:visited,
a:active {
  color: #76f0ff;
  text-decoration: none;
}
b {
  font-weight: bold;
}
.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #262626;
  z-index: -1;
}
.toneOn .bg {
  background-color: #404860;
}
button:not([disabled]) {
  cursor: pointer;
}
header {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 640px) {
  header {
    justify-content: center;
  }
}
@media (min-width: 1024px) {
  header {
    flex-direction: row-reverse;
  }
}
header .siteName {
  font-family: Audiowide;
  font-size: 30px;
  text-align: center;
  margin: 0 0.6rem;
}
@media (min-width: 480px) {
  header .siteName {
    margin: 0.6rem;
  }
}
@media (min-width: 1024px) {
  header .siteName {
    font-size: 40px;
    width: 340px;
    margin: 0;
  }
}
header .siteName > a {
  color: #e4e4e4;
  text-decoration: underline;
}
nav.menu {
  flex: 1 0;
  background: linear-gradient(#f1f1f1, #dedede);
  text-align: center;
  border: 1px solid #fff;
  box-shadow: 0 0 12px -1px #fff;
  margin: 0.6rem 0 0.6rem 0.6rem;
  line-height: 1.3;
}
@media (min-width: 0) {
  nav.menu {
    min-width: 280px;
  }
}
@media (min-width: 480px) {
  nav.menu {
    min-width: 410px;
  }
}
@media (min-width: 768px) {
  nav.menu {
    min-width: initial;
    margin: 1rem 0 1rem 1rem;
  }
}
@media (min-width: 1024px) {
  nav.menu {
    margin: 1rem 0;
    min-width: 440px;
  }
}
nav.menu:hover {
  box-shadow: 0 0 11px 1px #fff;
}
nav.menu .btn {
  color: #262626;
}
@media (min-width: 0) {
  nav.menu .btn {
    padding: 0.6rem 0.3rem;
    font-size: 1rem;
  }
}
@media (min-width: 480px) {
  nav.menu .btn {
    padding: 0.6rem;
  }
}
@media (min-width: 640px) {
  nav.menu .btn {
    font-size: 1rem;
  }
}
@media (min-width: 1024px) {
  nav.menu .btn {
    padding: 0.6rem;
  }
}
nav.menu .btn:hover {
  background: #e5e5e5;
  text-shadow: 0 0 3px #fff;
}
nav.menu .btn:active {
  background: #fff;
}
nav.menu .btn.active {
  background: linear-gradient(#d1d7da, #f1f1f1);
  box-shadow: 0 1px 6px #444;
  color: #111;
  cursor: default;
}
.controls {
  font-size: 0;
  text-align: center;
  margin: 0 0.6rem 0 0;
}
@media (min-width: 480px) {
  .controls {
    margin: 0 0.6rem;
  }
}
@media (min-width: 1024px) {
  .controls {
    margin: 0.6rem;
    min-width: initial;
    margin-left: 1rem;
  }
}
.controls .btn {
  background: url(/img/media-buttons-48.png);
  width: 48px;
  height: 48px;
  border: none;
}
.controls .btn:hover {
  background-position-y: -96px;
}
.controls .btn[disabled] {
  color: #999;
  background-position-y: -48px;
}
.controls .btn-start {
  background-position-x: -96px;
}
.controls .btn-pause {
  background-position-x: 0;
}
.controls .btn-stop {
  background-position-x: -48px;
}
#pasteText {
  flex: 1;
  margin-bottom: 2rem;
  line-height: 1.5;
  background-color: #0c0c0c;
  color: #e4e4e4;
}
.columns {
  flex: 1;
  min-height: 270px;
  overflow-y: auto;
}
@media (min-width: 1024px) {
  .columns {
    display: flex;
    margin: 0;
  }
}
.instructions {
  margin: 2rem auto;
  font-size: 1.1em;
  border-spacing: 1rem;
}
.instructions td {
  display: inline-block;
}
@media (min-width: 480px) {
  .instructions {
    border-spacing: 0;
  }
  .instructions td {
    display: table-cell;
  }
}
.instructions td {
  padding: 0.6rem;
}
.instructions kbd {
  border: 1px solid #ccc;
  border-radius: 4px;
}
.instructions kbd,
.instructions a {
  display: block;
  padding: 1px 0.6rem;
  text-align: center;
  cursor: pointer;
}
.controls.disabled,
.menu.disabled,
.sidebar.disabled {
  opacity: 0;
  transition: opacity 0.4s;
}
.views {
  flex: 1;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .views {
    height: 100%;
    overflow: hidden;
  }
}
.view {
  position: relative;
  flex: 1;
  width: 100%;
  background: #161616;
}
.view article {
  margin: 1rem auto 0;
  flex: 1;
  width: 95%;
}
@media (min-width: 1024px) {
  .view article {
    margin-top: 2rem;
  }
}
@media (min-width: 1280px) {
  .view article {
    max-width: 80%;
  }
}
.view article h2 {
  font-size: 2rem;
  margin-top: 0;
  background: #e4e4e4;
  color: #0c0c0c;
  text-align: center;
  padding: 0 0.6rem;
}
@media (min-width: 1024px) {
  .view {
    overflow-y: auto;
  }
}
.view.disabled {
  display: none !important;
}
.view.home article h2 {
  margin: 2rem 0;
}
@media (min-width: 769px) {
  .view.home .sections {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1rem;
  }
}
.view.home .sections section {
  flex: 1 0 320px;
  margin: 0 1rem;
}
.view.home .sections .story-previews .title {
  font-size: 1.5rem;
  margin: 0;
}
.view.home .sections .story-previews .byline {
  margin: 0 2rem;
  font-style: italic;
  color: #c2c2c2;
}
.view.home .sections .story-previews .author {
  font-weight: bold;
}
.view.home .sections .story-previews .more {
  font-size: 1.5rem;
  text-align: right;
}
.view.home .sections .story-previews .blurb {
  margin: 0 0 1rem;
}
.view.paste {
  display: flex;
  flex-direction: column;
}
.view.paste article {
  display: flex;
  flex-direction: column;
}
.view.stories .story {
  margin-bottom: 2rem;
  border-left: 4px solid #76f0ff;
  border-radius: 8px;
  padding-left: 1rem;
}
.view.stories .story .blurb,
.view.stories .story .byline {
  color: #c2c2c2;
  font-style: italic;
}
.view.stories .story .title {
  margin: 0;
  line-height: 1;
}
.view.stories .story .author {
  font-weight: bold;
}
.view.stories .story .byline {
  margin-top: 0;
  margin-left: 1rem;
}
.view.playing {
  display: flex;
  flex-direction: column;
}
.view.playing article {
  display: flex;
  flex-direction: column;
}
.view.playing .patternWrap {
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
  padding-bottom: 1rem;
  font-size: 3px;
}
@media (min-width: 640px) {
  .view.playing .patternWrap {
    font-size: 4px;
  }
}
.view.playing .pattern {
  height: 4em;
  float: right;
}
.view.playing .pattern .element {
  border-radius: 2em;
  height: 4em;
  display: inline-block;
  margin-left: 0.25em;
  margin-right: 0.25em;
}
.view.playing .pattern .element.dit {
  width: 4em;
  background-color: #76f0ff;
}
.view.playing .pattern .element.dah {
  width: 12em;
  background-color: #76f0ff;
}
.view.playing .pattern .element.charSpace {
  width: 4em;
}
.view.playing .pattern .element.wordSpace {
  width: 6em;
}
.letter {
  flex: 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  display: flex;
  color: #76f0ff;
}
@media (min-width: 0) {
  .letter {
    font-size: 30px;
  }
}
@media (min-width: 480px) {
  .letter {
    font-size: 50px;
  }
}
@media (min-width: 640px) {
  .letter {
    font-size: 70px;
  }
}
@media (min-width: 769px) {
  .letter {
    font-size: 80px;
  }
}
@media (min-width: 1024px) {
  .letter {
    font-size: 70px;
  }
}
@media (min-width: 1281px) {
  .letter {
    font-size: 100px;
  }
}
@media (min-width: 1600px) {
  .letter {
    font-size: 120px;
  }
}
.letter:empty::before {
  content: '\200b';
}
.sidebar {
  flex-shrink: 0;
  overflow-y: auto;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
}
@media (min-width: 0) {
  .sidebar {
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .sidebar {
    width: 340px;
  }
}
.sidebar .social {
  margin-top: 1rem;
  margin-bottom: 40px;
  height: 28px;
}
.sidebar .sidebarContent {
  padding: 20px 0;
  flex: 1;
}
.sidebar .sidebarContent .ad {
  display: inline-block;
  width: 300px;
  height: 250px;
  background: #262626;
}
.sidebar .info {
  margin-top: 1rem;
}
.sidebar .info a {
  color: #a8a8a8;
  font-size: 1rem;
}
.btn-fullscreen {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  border: 0;
  margin: 0;
  padding: 1rem 2rem;
  font-weight: bold;
  background: transparent;
  color: #e4e4e4;
  outline: none;
}
.btn-fullscreen.disabled {
  display: none;
}
.outputBuffer {
  flex: 1;
  min-height: 250px;
  max-height: 1000px;
  overflow-wrap: break-word;
  overflow-y: hidden;
  margin-top: 1rem;
  padding: 0.6rem 1rem;
  background: linear-gradient(#0c0c0c, #161616);
  color: #a79e1e;
  font-family: monospace;
  font-size: 1.5rem;
  letter-spacing: 3px;
  white-space: pre-wrap;
  word-break: break-word;
}
@media (min-width: 1600px) {
  .outputBuffer {
    font-size: 2rem;
  }
}
@media (min-width: 1920px) {
  .outputBuffer {
    font-size: 2.3rem;
  }
}
.settings table {
  width: 100%;
  margin-bottom: 2rem;
  border-spacing: 0;
}
.settings table th {
  text-align: right;
  padding: 0.6rem;
  font-weight: bold;
}
.settings table td {
  padding: 0.6rem 0.6rem;
  border-left: 5px solid #262626;
}
@media (max-width: 479px) {
  .settings table th,
  .settings table td {
    display: block;
    text-align: left;
    padding: 0 0 0 0.6rem;
  }
  .settings table td:last-of-type {
    margin-bottom: 2rem;
  }
}
.settings table td:last-of-type {
  width: 100%;
  padding: 0.6rem 0 0.6rem 0.6rem;
}
.settings table td:first-of-type {
  font-family: monospace;
  color: #76f0ff;
  font-size: larger;
  text-align: center;
}
.settings table input[type=range] {
  width: 100%;
}
